'use client';

import { useState, useEffect, useCallback, useRef } from 'react';

const Game = ({ username }: { username: string }) => {
  // Upphafsstillingar fyrir leikborðið, boltann og skórinn
  const [paddleY, setPaddleY] = useState<number>(200); // Leikmannspallur (vinstri)
  const [aiPaddleY, setAiPaddleY] = useState<number>(200); // AI pallur (hægri)
  const [ballX, setBallX] = useState<number>(450);         // Boltans X-staðsetning
  const [ballY, setBallY] = useState<number>(300);         // Boltans Y-staðsetning

  const [ballSpeedX, setBallSpeedX] = useState<number>(5);   // Hraði boltans á X-ás
  const [ballSpeedY, setBallSpeedY] = useState<number>(5);   // Hraði boltans á Y-ás
  const [playerScore, setPlayerScore] = useState<number>(0); // Leikmannsstig
  const [aiScore, setAiScore] = useState<number>(0);         // AI stig
  const [gameOver, setGameOver] = useState<boolean>(false);  // Ef leikurinn er búinn

  // State fyrir countdown-timerinn og hvort leikurinn hafi byrjað
  const [countdown, setCountdown] = useState<number>(3);
  const [gameStarted, setGameStarted] = useState<boolean>(false);

  // Notum useRef til að koma í veg fyrir endurteknar kollisjónir og tvöföld stigaskráning
  const collisionCooldownRef = useRef(false);
  const scoreCooldownRef = useRef(false);
  const ballRadius = 5; // Bolti er 10px x 10px (radíus = 5px)

  // Hreyfing leikmannspallsins með örvatökum
  const handleKeyDown = (e: KeyboardEvent) => {
    setPaddleY((prev) => {
      if (e.key === 'ArrowUp' && prev > 0) {
        return prev - 10;
      }
      if (e.key === 'ArrowDown' && prev < 500) {
        return prev + 10;
      }
      return prev;
    });
  };

  useEffect(() => {
    window.addEventListener('keydown', handleKeyDown);
    return () => window.removeEventListener('keydown', handleKeyDown);
  }, []);

  // Ef leikurinn ekki byrjaður (gameStarted er false), lækkum countdownið um 1 í hverri sekúndu
  useEffect(() => {
    if (!gameStarted) {
      if (countdown > 0) {
        const timer = setTimeout(() => {
          setCountdown(countdown - 1);
        }, 1000);
        return () => clearTimeout(timer);
      } else {
        setGameStarted(true);
      }
    }
  }, [countdown, gameStarted]);

  // Reiknar út nýja boltahraða í Y átt miðað við hvar boltinn lendir á palli
  const calculateBounceAngle = useCallback(
    (paddleY: number, ballY: number): number => {
      const paddleCenter = paddleY + 50; // miðja pallsins (pallinn er 100px hár)
      const ballCenter = ballY + ballRadius; // miðja boltans
      const relativeIntersection = ballCenter - paddleCenter;
      const normalizedRelativeIntersection = relativeIntersection / 50; // 50 = hálfur hæð palls
      return normalizedRelativeIntersection * 1.5; // Stýrir breyttum hraða
    },
    [ballRadius]
  );

  // Endurstillir boltann þegar mark er skráð
  const resetBall = (whoGetsBall: string) => {
    setBallX(450);
    setBallY(300);
    const baseSpeed = 5;
    setBallSpeedY(baseSpeed);

    if (whoGetsBall === 'player') {
      setBallSpeedX(baseSpeed); // Boltinn fer til hægri
    } else {
      setBallSpeedX(-baseSpeed); // Boltinn fer til vinstri
    }
  };

  useEffect(() => {
    if (!gameStarted) return; // Ekki keyra leiklykkjuna fyrr en teljari er búinn

    const interval = setInterval(() => {
      // Uppfærum boltastaðsetningu á X ásnum
      setBallX((prevX) => {
        const newX = prevX + ballSpeedX;
        // Ef boltinn fer út um vinstri hlið (mark fyrir AI)
        if (!scoreCooldownRef.current && newX - ballRadius <= 0) {
          scoreCooldownRef.current = true;
          setAiScore((prevScore) => prevScore + 1);
          resetBall('player');
          setTimeout(() => {
            scoreCooldownRef.current = false;
          }, 100);
          return 450;
        }
        // Ef boltinn fer út um hægri hlið (mark fyrir leikmann)
        if (!scoreCooldownRef.current && newX + ballRadius >= 900) {
          scoreCooldownRef.current = true;
          setPlayerScore((prevScore) => prevScore + 1);
          resetBall('AI');
          setTimeout(() => {
            scoreCooldownRef.current = false;
          }, 100);
          return 450;
        }
        return newX;
      });

      // Uppfærum boltastaðsetningu á Y ásnum og athugum veggja
      setBallY((prevY) => {
        let newY = prevY + ballSpeedY;
        if (newY <= 0) {
          newY = 0;
          setBallSpeedY((prev) => Math.abs(prev));
        } else if (newY >= 590) {
          newY = 590;
          setBallSpeedY((prev) => -Math.abs(prev));
        }
        return newY;
      });

      // Reiknum miðjuna á boltanum
      const ballCenterX = ballX + ballRadius;
      const ballCenterY = ballY + ballRadius;

      // Kollisjón við leikmannspallinn (vinstri)
      if (
        !collisionCooldownRef.current &&
        ballCenterX <= 40 &&
        ballCenterY >= paddleY &&
        ballCenterY <= paddleY + 100
      ) {
        collisionCooldownRef.current = true;
        setBallSpeedX((prevSpeed) => -prevSpeed);
        const bounceAngle = calculateBounceAngle(paddleY, ballY);
        setBallSpeedY(bounceAngle);
        setBallX(41);
        setTimeout(() => {
          collisionCooldownRef.current = false;
        }, 50);
      }

      // Kollisjón við AI-pallinn (hægri)
      if (
        !collisionCooldownRef.current &&
        ballCenterX >= 860 &&
        ballCenterX <= 880 &&
        ballCenterY >= aiPaddleY &&
        ballCenterY <= aiPaddleY + 100
      ) {
        collisionCooldownRef.current = true;
        setBallSpeedX((prevSpeed) => -prevSpeed);
        const bounceAngle = calculateBounceAngle(aiPaddleY, ballY);
        setBallSpeedY(bounceAngle);
        setBallX(860 - 10);
        setTimeout(() => {
          collisionCooldownRef.current = false;
        }, 50);
      }

      // AI pallurinn fylgir miðju boltans
      setAiPaddleY((prev) => {
        if (ballCenterY > prev + 50 && prev < 500) {
          return prev + 2;
        } else if (ballCenterY < prev + 50 && prev > 0) {
          return prev - 2;
        }
        return prev;
      });

      if (playerScore >= 10 || aiScore >= 10) {
        setGameOver(true);
      }
    }, 10);

    return () => clearInterval(interval);
  }, [
    gameStarted,
    ballSpeedX,
    ballSpeedY,
    paddleY,
    aiPaddleY,
    playerScore,
    aiScore,
    ballX,
    ballY,
    calculateBounceAngle,
    ballRadius,
  ]);

  if (gameOver) {
    return (
      <div style={{ textAlign: 'center', fontFamily: 'sans-serif' }}>
        <h2>
          {username} {playerScore} - {aiScore} AI
        </h2>
        <h3>Leikurinn er búinn! {playerScore >= 10 ? username : "AI"} vann!</h3>
        <button
          style={{
            marginTop: '20px',
            padding: '10px 20px',
            fontSize: '1.2rem',
            cursor: 'pointer',
          }}
          onClick={() => {
            // Endurstilla allar stöður til að byrja leikinn upp úr nýju
            setPaddleY(200);
            setAiPaddleY(200);
            setBallX(450);
            setBallY(300);
            setBallSpeedX(5);
            setBallSpeedY(5);
            setPlayerScore(0);
            setAiScore(0);
            setGameOver(false);
            setCountdown(3);
            setGameStarted(false);
          }}
        >
          Keppa aftur?
        </button>
      </div>
    );
  }

  return (
    <div style={{ fontFamily: 'sans-serif', textAlign: 'center', position: 'relative' }}>
      <h2>
        {username} {playerScore} - {aiScore} AI
      </h2>
      <div
        style={{
          width: '900px',
          height: '600px',
          margin: 'auto',
          border: '3px solid #333',
          borderRadius: '8px',
          background: 'linear-gradient(135deg, #f0f0f0, #d0d0d0)',
          boxShadow: '0 4px 8px rgba(0,0,0,0.3)',
          position: 'relative',
        }}
      >
        {/* Countdown overlay - birtist þangað til leikurinn byrjar */}
        {!gameStarted && (
          <div
            style={{
              position: 'absolute',
              top: 0,
              left: 0,
              right: 0,
              bottom: 0,
              backgroundColor: 'rgba(0,0,0,0.5)',
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
              zIndex: 10,
              fontSize: '5rem',
              color: 'white',
            }}
          >
            {countdown > 0 ? countdown : 'GO!'}
          </div>
        )}

        {/* Vinstri og hægri veggir */}
        <div
          style={{
            position: 'absolute',
            left: '0',
            top: '0',
            width: '10px',
            height: '100%',
            backgroundColor: '#333',
            borderRadius: '0 4px 4px 0',
          }}
        />
        <div
          style={{
            position: 'absolute',
            right: '0',
            top: '0',
            width: '10px',
            height: '100%',
            backgroundColor: '#333',
            borderRadius: '4px 0 0 4px',
          }}
        />

        {/* Leikmannspallur */}
        <div
          style={{
            position: 'absolute',
            left: '20px',
            top: paddleY,
            width: '20px',
            height: '100px',
            backgroundColor: '#0077cc',
            borderRadius: '4px',
          }}
        />

        {/* AI pallur */}
        <div
          style={{
            position: 'absolute',
            left: '860px',
            top: aiPaddleY,
            width: '20px',
            height: '100px',
            backgroundColor: '#cc0000',
            borderRadius: '4px',
          }}
        />

        {/* Bolti */}
        <div
          style={{
            position: 'absolute',
            left: ballX,
            top: ballY,
            width: '10px',
            height: '10px',
            backgroundColor: '#28a745',
            borderRadius: '50%',
          }}
        />
      </div>
    </div>
  );
};

export default Game;
